import ReactDOM from "react-dom";
import React, { useState } from "react";
import { useSpring, animated } from "react-spring";
import range from "lodash-es/range";
import "../styles/Jump.css";
import {
    PageHeader,
    Typography,
    Modal,
    Button,
    Form,
    Input,
    message,
} from "antd";
import { GithubFilled, LinkedinFilled, MailFilled } from "@ant-design/icons";
import { useFirebaseApp } from "reactfire";

const { Text, Link } = Typography;
const items = range(1);
const interp = (i) => (r) =>
    `translate3d(0, ${15 * Math.sin(r + (i * 2 * Math.PI) / 1.6)}px, 0)`;

export default function Jump() {
    const [visibleModalMail, setVisibleModalMail] = useState(false);
    const [confirmLoadingModalMail, setconfirmLoadingModalMail] = useState(
        false
    );
    const alertDone = () => {
        message.success(
            "Joan Sebastian contactará contigo en el menor tiempo posible"
        );
    };
    const firebaseApp = useFirebaseApp();
    const handleOkModalMail = (values) => {
        setconfirmLoadingModalMail(true);
        firebaseApp
            .firestore()
            .collection("contactos")
            .add(values.contact)
            .then(() => {
                setVisibleModalMail(false);
                setconfirmLoadingModalMail(false);
                alertDone();
            })
            .catch(() => {
                console.error("Error");
            });
    };

    const handleCancelModalMail = () => {
        setVisibleModalMail(false);
    };

    const { radians } = useSpring({
        to: async (next) => {
            while (1) await next({ radians: 2 * Math.PI });
        },
        from: { radians: 0 },
        config: { duration: 3500 },
        reset: true,
    });

    const showModalMail = () => {
        setVisibleModalMail(true);
    };

    const layout = {
        labelCol: {
            span: 8,
        },
        wrapperCol: {
            span: 24,
        },
    };

    const validateMessages = {
        required: "!${label} es obligatorio!",
        types: {
            email: "!${label} el correo electrónico no es válido!",
        },
    };

    return (
        <animated.div
            key={1}
            style={{ transform: radians.interpolate(interp(1)) }}>
            <Link onClick={showModalMail}>
                <MailFilled style={{ color: "#FFF269", fontSize: 40, marginRight:16 }} />
            </Link>
            ,
            <Link href='https://www.linkedin.com/in/jojanes/' target='_blank'>
                <LinkedinFilled style={{ color: "#FFF269", fontSize: 40, marginRight:12 }} />
            </Link>
            ,
            <Link href='https://github.com/jojanes011' target='_blank'>
                <GithubFilled style={{ color: "#FFF269", fontSize: 40 }} />
            </Link>
        </animated.div>
    );
}
