import React from "react";
import {
    Row,
    Col,
    Typography,
    Space,
    Tooltip,
    Avatar,
    Button,
    Divider,
} from "antd";
import { DownloadOutlined, UserOutlined } from "@ant-design/icons";
import Title from "antd/lib/skeleton/Title";
import { useStorage } from "reactfire";

export default function Me(props) {
    const firebaseApp = useStorage();
    const { Text } = Typography;

    const downloadCV = () => {
        firebaseApp.ref(
            "fundacion-PG-Ingenieria-informatica (plan de estudios).pdf"
        );
        console.log("hola");
    };

    return (
        <Row style={{ marginBottom: 70 }}>
            <Col span={14}>
                <Row style={{ padding: 60 }}>
                    <Text
                        style={{
                            color: "#FFFFFF",
                            textAlign: "justify",
                            fontSize: 24,
                        }}>
                        Tecnólogo en análisis y desarrollo de sistemas de
                        información en el SENA. Con experiencia profesional en
                        la creación de software y especializado en aplicaciones
                        móviles. Brindo mis servicios como programador web para
                        el desarrollo de sitios personales, corporativos y
                        tiendas E-commerce con un planteamiento llamativo y SEO.
                        He trabajado como desarrollador para varias compañías.
                    </Text>
                </Row>
                <Row>
                    <Col span={4}></Col>
                    <Col span={16}>
                        <Space size={70}>
                            <Tooltip title='PHP'>
                                <img
                                    src={require("../assets/img/php.png")}
                                    style={{
                                        width: 60,
                                        height: "auto",
                                    }}
                                    alt='logo'
                                />
                            </Tooltip>
                            <Tooltip title='JavaScript'>
                                <img
                                    src={require("../assets/img/javascript.png")}
                                    style={{
                                        width: 60,
                                        height: "auto",
                                    }}
                                    alt='logo'
                                />
                            </Tooltip>
                            <Tooltip title='Python'>
                                <img
                                    src={require("../assets/img/python.png")}
                                    style={{
                                        width: 60,
                                        height: "auto",
                                    }}
                                    alt='logo'
                                />
                            </Tooltip>
                            <Tooltip title='Kotlin'>
                                <img
                                    src={require("../assets/img/kotlin.png")}
                                    style={{
                                        width: 60,
                                        height: "auto",
                                    }}
                                    alt='logo'
                                />
                            </Tooltip>
                        </Space>
                    </Col>
                    <Col></Col>
                </Row>
                <Row style={{ marginTop: 30 }}>
                    <Col span={4}></Col>
                    <Col>
                        <Space size={70}>
                            <Tooltip title='Laravel'>
                                <img
                                    src={require("../assets/img/laravel.png")}
                                    style={{
                                        width: 60,
                                        height: "auto",
                                    }}
                                    alt='logo'
                                />
                            </Tooltip>
                            <Tooltip title='React JS'>
                                <img
                                    src={require("../assets/img/react.png")}
                                    style={{
                                        width: 60,
                                        height: "auto",
                                    }}
                                    alt='logo'
                                />
                            </Tooltip>
                            <Tooltip title='Django'>
                                <img
                                    src={require("../assets/img/django.png")}
                                    style={{
                                        width: 60,
                                        height: "auto",
                                    }}
                                    alt='logo'
                                />
                            </Tooltip>
                            <Tooltip title='React Native'>
                                <img
                                    src={require("../assets/img/react.png")}
                                    style={{
                                        width: 60,
                                        height: "auto",
                                    }}
                                    alt='logo'
                                />
                            </Tooltip>
                        </Space>
                    </Col>
                    <Col></Col>
                </Row>
            </Col>
            <Col span={10}>
                <Avatar
                    src={require("../assets/img/Perfil.png")}
                    icon={<UserOutlined />}
                    size={300}
                    style={{ marginLeft: 100, marginTop: 40 }}
                    shape='circle'
                />
                <Divider />
                <Title level={2} style={{ color: "#82E5FF", marginLeft: 45 }}>
                    Joan Sebastian Oviedo Teran
                </Title>
                <Divider />
                <Button
                    shape='round'
                    onClick={downloadCV}
                    style={{
                        backgroundColor: "#FF9CBF",
                        color: "black",
                        borderColor: "black",
                        marginLeft: 165,
                    }}
                    icon={<DownloadOutlined />}
                    size='large'>
                    Descargar CV
                </Button>
            </Col>
        </Row>
    );
}
