import React from "react";
import { Row } from "antd";
import Card from "../components/Card";

export default function Portfolio() {
    const { Meta } = Card;
    return (
        <div className='site-card-wrapper'>
            <Row style={{ padding: 20 }} justify='space-around'>
                <Card />
                <Card />
                <Card />
            </Row>
            <Row style={{ padding: 20 }} justify='space-around'>
                <Card />
                <Card />
                <Card />
            </Row>
        </div>
    );
}
