import React, { useState } from "react";
import "../styles/Navbar.css";
import { PageHeader, Typography, Modal, Button, Form, Input, message } from "antd";
import { GithubFilled, LinkedinFilled, MailFilled } from "@ant-design/icons";
import { useFirebaseApp } from "reactfire";
import "firebase/firestore";
import Jump from "./Jump";

export default function Navbar(props) {
    const firebaseApp = useFirebaseApp();
    const { Text, Link } = Typography;
    const [visibleModalMail, setVisibleModalMail] = useState(false);
    const [confirmLoadingModalMail, setconfirmLoadingModalMail] = useState(
        false
    );

    const layout = {
        labelCol: {
            span: 8,
        },
        wrapperCol: {
            span: 24,
        },
    };

    const validateMessages = {
        required: "!${label} es obligatorio!",
        types: {
            email: "!${label} el correo electrónico no es válido!",
        }
    };

    const showModalMail = () => {
        setVisibleModalMail(true);
    };

    const alertDone = () => {
        message.success('Joan Sebastian contactará contigo en el menor tiempo posible');
    }

    const handleOkModalMail = (values) => {
        setconfirmLoadingModalMail(true);
        firebaseApp
            .firestore()
            .collection("contactos")
            .add(values.contact)
            .then(() => {
                setVisibleModalMail(false);
                setconfirmLoadingModalMail(false);
                alertDone();
            })
            .catch(() => {
                console.error("Error");
            });
    };

    const handleCancelModalMail = () => {
        setVisibleModalMail(false);
    };

    return (
        <>
            <PageHeader
                style={{marginTop: 16}}
                title={
                    <img
                        src={require("../assets/img/logo2.png")}
                        style={{ width: 200, height: "auto" }}
                        alt='logo'
                    />
                }
                ghost={true}
                className='site-page-header'
                subTitle={
                    <Text style={{ color: "#82E5FF" }}>
                        Joan Sebastian Oviedo | Desarrollador WEB
                    </Text>
                }
                extra={[
                    <Jump />
                    // <Link onClick={showModalMail}>
                    //     <MailFilled
                    //         style={{ color: "#FFF269", fontSize: 40 }}
                    //     />
                    // </Link>,
                    // <Link
                    //     href='https://www.linkedin.com/in/jojanes/'
                    //     target='_blank'>
                    //     <LinkedinFilled
                    //         style={{ color: "#FFF269", fontSize: 40 }}
                    //     />
                    // </Link>,
                    // <Link href='https://github.com/jojanes011' target='_blank'>
                    //     <GithubFilled
                    //         style={{ color: "#FFF269", fontSize: 40 }}
                    //     />
                    // </Link>,
                ]}></PageHeader>
        </>
    );
}
