import React from "react";
import "../styles/App.css";
import Navbar from "../components/Navbar";
import Portfolio from "../components/Portfolio";
import FooterInitial from "../components/FooterInitial";
import Me from "../components/Me";
import { Layout, BackTop, Divider, Typography } from "antd";

function App() {
    const { Header, Content, Sider, Footer } = Layout;
    const { Title } = Typography;
    const style = {
        height: 40,
        width: 40,
        lineHeight: "40px",
        borderRadius: 4,
        backgroundColor: "#82E5FF",
        color: "black",
        textAlign: "center",
        fontSize: 14,
    };
    return (
        <Layout>
            <Header>
                <Navbar />
            </Header>
            <Layout style={{ backgroundColor: "#011526" }}>
                {/* <Sider
                    style={{
                        overflow: "auto",
                        height: "100vh",
                        position: "fixed",
                        left: 0,
                    }}>
                    <SiderSteps />
                </Sider> */}
                <Content style={{ marginTop: 32 }}>
                    <Me />
                    <Divider><Title style={{color: '#82E5FF'}}>Portafolio</Title></Divider>
                    <Portfolio />
                    <FooterInitial />
                </Content>
            </Layout>
            <BackTop>
                <div style={style}>UP</div>
            </BackTop>
        </Layout>
    );
}

export default App;
